import React,{Component} from 'react'
import { hashHistory} from 'react-router';
import { Form, Input, Button, Radio, Spin, message } from 'antd';
const FormItem = Form.Item;
export class DataForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: "",
            link: "",
            loading: false
        };
        ['handleSubmit', 'resetForm'].forEach(m=>this[m] = this[m].bind(this))
    }

    componentWillMount() {
        let that = this;
        if(this.props.type == "edit") {
            this.setState({ loading: true });
            ajax({
                url: "/link/getInfoDetail",
                data: {
                    id: this.props.id
                },
                callback: function(data) {
                    if(data.status == 200) {
                        let { name, link } = data.data.detailInfo;
                        that.setState({
                            loading: false,
                            name,
                            link
                        });
                    } else {
                        message.error(data.errMsg, 1);
                    }
                }
            });
        }
    }

    handleSubmit(e) {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if (!err) {
                if(this.props.type == "edit") {
                    values = Object.assign(values, {id: this.props.id});
                }
                ajax({
                    url: "/link/updateInfo",
                    data: values,
                    callback: function(data) {
                        if(data.status == 200) {
                            message.success("设置成功!", 1, function() {
                                hashHistory.push("/link")
                            });
                        } else {
                            message.error(data.errMsg, 1)
                        }
                    }
                });
            }
        });
    }

    returnList() {
        hashHistory.push("/link");
    }

    resetForm() {
        this.props.form.resetFields();
    }

    render() {
        const { getFieldDecorator } = this.props.form;
        const formItemLayout = {
            labelCol: { span: 6 },
            wrapperCol: { span: 6 }
        };
        return (
            <Spin spinning={this.state.loading} tip="Loading...">
                <Form>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="链接名称"
                        hasFeedback
                    >
                        {getFieldDecorator('name', {
                            rules: [
                                { required: true, message: '请输入名称!' }
                            ],
                            initialValue: this.state.name
                        })(
                            <Input type="text" />
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="链接地址"
                        hasFeedback
                    >
                        {getFieldDecorator('link', {
                            rules: [
                                { required: true, message: '请输入链接地址!' }
                            ],
                            initialValue: this.state.link
                        })(
                            <Input type="text" />
                        )}
                    </FormItem>
                    <FormItem
                        wrapperCol={{ span: 12, offset: 6 }}
                    >
                        { !this.state.disabled ? <Button type="primary" onClick={this.handleSubmit} style={{"marginRight": "20px"}}>提交</Button> : ""}

                        <Button onClick={this.returnList} style={{"marginRight": "20px"}}>取消</Button>
                        { !this.state.disabled ? <Button type="danger" onClick={this.resetForm}>重置</Button> : ""}
                    </FormItem>
                </Form>
            </Spin>
        )
    }
}

const WrappedDataForm = Form.create({})(DataForm);

module.exports = WrappedDataForm;


